<template id="">
  <div id="app">
    <openApp @saveToCloud="saveToCloud" @goApp="goApp"/>
  </div>
</template>

<script type="text/javascript">
import openApp from './components/openApp.vue'
import { _openApp } from './utils/index'
// import {_openApp} from './utils/app'
  export default {
    data() {
      return {
      }
    },
    components: {
      openApp
    },
    methods: {
      saveToCloud() {
        alert('saveToCloud-(去掉toLower函数)')
        console.log('cloud--log');
      },
       isQQ(){
        return navigator.userAgent.toLowerCase().includes("qqbrowser")
      },
      goApp() {
        var goAppInfo = {
          link: `http://caiyun.feixin.10086.cn/dl/185CjzcOjFTwe`,
          pwd: 'UlndMd',
          category: '00019700101000000001/1811hjJdT00Z05620191017152733yik',
          account: '19802021498',
          name: 'picture'
        }
        let jsonInfo = JSON.stringify(goAppInfo)
        let base64Info = window.btoa(jsonInfo)
        // _openApp(base64Info)
        console.log(`my iframe base64info ${base64Info}`);
        console.log(`goAppInfo ${atob(base64Info)}`);
        var frame = document.createElement('iframe');
        frame.src = `mcloud://outlinks?outlink=${base64Info}`;
        frame.style.display = 'none';
        document.body.appendChild(frame);

        // the following is optional, just to avoid an unnecessary iframe on the page
        setTimeout(function() { document.body.removeChild(frame); }, 4);
      }
    }
  }
</script>
